import React, { Component } from 'react';
import { connect } from 'react-redux';

import { 
    ACCOUNT_TYPE,
    USER_NAME,
    PHONE_NUMBER,
    EMAIL,
    WECHAT,
    REGISTE_TIME,
    AUTHORIZE_SERVICES,
} from '../../config/copywriting';
import {
    SERVICE_LEVEL_NAME,
} from '../../config/const';
import WechatUnificationService from '../../service/WechatUnificationService';

import styles from './index.module.scss';

import defaultAvator from '../../assets/default_avatar.png';

class AccountInfo extends Component {

    state = {
        avatarUrl: '',
        nickname: '-',
        serviceLevel: '',
        mobile: '',
        email: '',
        wxNickname: '',
        createTime: '',
    }

    componentDidMount() {        
        const { wechatSessionId } = this.props;
        // WechatUnificationService.GetYxUserInfo(wechatSessionId)
        //     .then((data) => {
        //         this.setState({ ...data.toObject() });
        //     })
        //     .catch((e) => {
        //         console.warn(e);
        //     });
    }

    redirectToServicesList = () => {
        this.props.history.push('/service-list');
    }

    render() {
        const { avatarUrl, nickname, serviceLevel,
                mobile, email, wxNickname, createTime,
        } = this.state;
        const lines = [
            {
                label: ACCOUNT_TYPE,
                value: SERVICE_LEVEL_NAME[serviceLevel],
            },
            {
                label: USER_NAME,
                value: nickname,
            },
            {
                label: PHONE_NUMBER,
                value: mobile,
            },
            {
                label: EMAIL,
                value: email,
            },
            {
                label: WECHAT,
                value: wxNickname,
            },
            {
                label: REGISTE_TIME,
                value: createTime,
            }
        ];
        return (
            <div className={styles.accountInfo}>
                <div className={`${styles.block} ${styles.top}`}>
                    <div className={styles.avator}>
                        <img src={avatarUrl || defaultAvator} alt=""></img>
                    </div>
                    <div className={styles.alias}>{nickname}</div>
                </div>
                <div className={styles.block}>
                    {lines.map(({ label, value }, index) => {
                        return (
                            <div className={styles.line} key={index}>
                                <div className={styles.label}>{label}</div>
                                <div className={styles.value}>{value}</div>
                            </div>
                        )
                    })}
                </div>
                <div className={styles.block}>
                    <div className={styles.line} onClick={this.redirectToServicesList}>
                        <div className={styles.label}>{AUTHORIZE_SERVICES}</div>
                        <div className={styles.value}>
                            <div className={styles.direct}></div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        wechatSessionId: state.wechatSessionId,
        setWechatJsReady: state.setWechatJsReady,
    };
};
  
export default connect(
    mapStateToProps,
)(AccountInfo);
